.wave-up,
.wave-down {
	z-index: 15;
	color: var(--white);
	position: fixed;
	width: calc(clamp(4px, 2vw, 50px));
	height: calc(clamp(4px, 2vw, 50px));
	left: 50%;
	transform: translateX(-50%);
	margin: 1% 0;
	border-radius: 50%;
}

.wave-up .to-up,
.wave-up .to-down,
.wave-down .to-up,
.wave-down .to-down {
	position: absolute;
	display: block;
	left: 50%;
	transform: translateX(-50%);
	width: calc(clamp(4px, 2vw, 50px));
	height: calc(clamp(4px, 2vw, 50px));
	border-radius: 50%;
	cursor: pointer;
}

@-webkit-keyframes wave {
	0% {
		transform: translate(-50%, -50%) scale(0);
	}
	50% {
		transform: translate(-50%, -50%) scale(1.2);
		opacity: 1;
	}
	100% {
		transform: translate(-50%, -50%) scale(1.5);
		opacity: 0;
	}
}

@keyframes wave {
	0% {
		transform: translate(-50%, -50%) scale(0);
	}
	50% {
		transform: translate(-50%, -50%) scale(1.2);
		opacity: 1;
	}
	100% {
		transform: translate(-50%, -50%) scale(1.5);
		opacity: 0;
	}
}

.wave-up::before,
.wave-down::before {
	content: "";
	width: 100%;
	height: 100%;
	transform: translate(-50%, -50%) scale(0);
	border: 1px solid var(--background-color3);
	border-radius: 50%;
	opacity: 0;
}

.wave-up .to-up::before,
.wave-up .to-up::after,
.wave-down .to-down::before,
.wave-down .to-down::after {
	content: "";
	position: absolute;
	display: block;
	inset: 0;
	margin: auto;
	width: 50%;
	height: 1px;
	background-color: var(--light-color);
}

.wave-up {
	visibility: hidden;
}

.wave-up:hover::before {
	-webkit-animation: wave 1.2s linear infinite;
	animation: wave 1.2s linear infinite;
}

.wave-up .to-up::before {
	transform: translateY(0) translateX(35%) rotate(45deg);
}

.wave-up .to-up::after {
	transform: translateY(0) translateX(-35%) rotate(-45deg);
}

.wave-up .to-up:hover::before {
	-webkit-animation: to-up-before 1.2s linear infinite;
	animation: to-up-before 1.2s linear infinite;
}

.wave-up .to-up:hover::after {
	-webkit-animation: to-up-after 1.2s linear infinite;
	animation: to-up-after 1.2s linear infinite;
}

.wave-up .to-up:hover::before,
.wave-up .to-up:hover::after {
	-webkit-animation-delay: 0.6s;
	animation-delay: 0.6s;
}

@-webkit-keyframes to-up-before {
	0% {
		transform: translateY(0) translateX(35%) rotate(45deg);
	}
	50% {
		transform: translateY(-300%) translateX(35%) rotate(45deg);
	}
	51% {
		transform: translateY(300%) translateX(35%) rotate(45deg);
	}
	100% {
		transform: translateY(0) translateX(35%) rotate(45deg);
	}
}

@keyframes to-up-before {
	0% {
		transform: translateY(0) translateX(35%) rotate(45deg);
	}
	50% {
		transform: translateY(-300%) translateX(35%) rotate(45deg);
	}
	51% {
		transform: translateY(300%) translateX(35%) rotate(45deg);
	}
	100% {
		transform: translateY(0) translateX(35%) rotate(45deg);
	}
}

@-webkit-keyframes to-up-after {
	0% {
		transform: translateY(0) translateX(-35%) rotate(-45deg);
	}
	50% {
		transform: translateY(-300%) translateX(-35%) rotate(-45deg);
	}
	51% {
		transform: translateY(300%) translateX(-35%) rotate(-45deg);
	}
	100% {
		transform: translateY(0) translateX(-35%) rotate(-45deg);
	}
}

@keyframes to-up-after {
	0% {
		transform: translateY(0) translateX(-35%) rotate(-45deg);
	}
	50% {
		transform: translateY(-300%) translateX(-35%) rotate(-45deg);
	}
	51% {
		transform: translateY(300%) translateX(-35%) rotate(-45deg);
	}
	100% {
		transform: translateY(0) translateX(-35%) rotate(-45deg);
	}
}

.wave-down:hover::before,
.wave-down.active::before {
	-webkit-animation: wave 1.2s linear infinite;
	animation: wave 1.2s linear infinite;
}

.wave-down .to-down::before {
	transform: translateX(35%) rotate(135deg);
}

.wave-down .to-down::after {
	transform: translateX(-35%) rotate(-135deg);
}

.wave-down .to-down:hover::before {
	-webkit-animation: to-down-before 1.2s linear infinite;
	animation: to-down-before 1.2s linear infinite;
}

.wave-down .to-down:hover::after {
	-webkit-animation: to-down-after 1.2s linear infinite;
