.nav-top {
	z-index: 120;
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	left: 90%;
	height: calc(clamp(50px, 4vw, 100px));
	display: inline-flex;
	align-items: center;
	justify-content: flex-end;
}

.nav-top .top-side {
	z-index: 20;
	height: 100%;
	display: inline-flex;
	align-items: center;
	justify-content: flex-end;
}

.nav-top .top-side li {
	color: var(--word-color);
	font-size: 0.8rem;
	margin-right: 3rem;
	cursor: pointer;
}

.nav-top .top-side li .page {
	position: relative;
	transition: transform 0.5s;
}

.nav-top .top-side li .page.active::before {
	position: absolute;
	content: "";
	height: calc(max(2px, 1%));
	width: 100%;
	background-color: var(--primary-color1);
	bottom: -15%;
}

.nav-top .top-side li .page::after {
	position: absolute;
	left: 50%;
	top: 50%;
	content: "";
	height: 0.8%;
	width: 100%;
	padding: 0 20%;
	background-color: var(--secondary-color1);
	transition: transform 0.5s;
	transform: translate(-50%, -50%) scaleX(0);
}

.nav-top .top-side li .page .link-width {
	opacity: 0;
}

.nav-top .top-side li .page span:not(.link-width) {
	transition: 0.5s;
}

.nav-top .top-side li .page span:not(.link-width):nth-child(2) {
	background-image: linear-gradient(to bottom, var(--word-color) 51%, transparent 49%, transparent);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

.nav-top .top-side li .page span:not(.link-width):nth-child(3) {
	background-image: linear-gradient(to top, var(--word-color) 51%, transparent 49%);
	-webkit-background-clip: text;
	background-size: 100%;
	-webkit-text-fill-color: transparent;
}

.nav-top .top-side li:hover .page span:not(.link-width) {
	transition-delay: transform 0.5s-0.2s;
}

.nav-top .top-side li:hover .page span:not(.link-width):nth-child(2) {
	left: 40%;
}

.nav-top .top-side li:hover .page span:not(.link-width):nth-child(3) {
	left: 60%;
}

.nav-top .top-side li:hover .page::after {
	transform: translate(-50%, -50%) scaleX(1);
}

.nav-top .nav-button {
	visibility: hidden;
	z-index: 20;
	position: absolute;
	top: 0;
	right: 0;
	height: 40px;
	width: 40px;
	cursor: pointer;
	border-radius: 5%;
	margin-right: 5px;
	margin-top: 5px;
}

.nav-top .nav-button .bar {
	position: absolute;
	width: 80%;
	height: 5%;
	inset: 0;
	margin: auto;
	background-color: var(--light-color);
	transition: transform 0.2s cubic-bezier(1, 0.14, 0.52, 0.85), width 0.3s;
}

.nav-top .nav-button .bar::before,
.nav-top .nav-button .bar::after {
	content: "";
	position: absolute;
	height: 100%;
	width: 50%;
	background-color: var(--light-color);
	transition: width 0.2s cubic-bezier(0.2, 0.93, 0.66, 0.9), transform 0.2s cubic-bezier(1, 0.14, 0.52, 0.85);
}

.nav-top .nav-button .bar::before {
	transform: translateY(-8px);
	left: 0;
}

.nav-top .nav-button .bar::after {
	transform: translateY(8px);
	right: 0;
}

.nav-top.active .bar {
	width: 50%;
	transform: rotate(45deg);
}

.nav-top.active .bar::before,
.nav-top.active .bar::after {
	width: 100%;
}

.nav-top.active .bar::before {
	transform: rotate(-90deg);
}

.nav-top.active .bar::after {
	transform: rotate(-90deg);
}

@media (max-width: 850px) {
