		margin-right: 0;
	}
	.nav-top .top-side li:hover {
		background-color: var(--background-color2);
	}
	.nav-top.active .top-side {
		transform: translateY(0);
	}
}

ul {
	z-index: 110;
}

ul.right-side {
	position: -webkit-sticky;
	position: sticky;
	display: inline-flex;
	flex-direction: column;
	align-items: flex-end;
	justify-content: center;
	top: 50%;
	transform: translateY(-50%);
	margin-right: 2%;
	left: 100%;
}

ul.right-side li {
	display: inline-block;
	margin: 70% 0;
	padding: calc(clamp(3.5px, 0.3vw, 6px));
	border-radius: 50%;
	box-shadow: inset 0px 0px 2px var(--light-color);
	background-color: var(--background-color2);
	cursor: pointer;
}

ul.right-side li.active {
	background-color: var(--primary-color1);
}

@media (max-width: 850px) {
	ul.right-side {
		position: fixed;
		top: auto;
		left: 50%;
		transform: translate(-50%, 0);
		bottom: 0;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		background-color: var(--background-color2);
		box-shadow: 0 -1px 3px var(--background-color3);
		width: 100%;
		height: 50px;
	}
	ul.right-side li {
		margin: 0 10px;
		position: relative;
	}
	ul.right-side li::before {
		z-index: -1;
		content: "";
		position: absolute;
		height: 50px;
		width: 400%;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
	}
	ul.right-side .back-page,
	ul.right-side .next-page {
		position: relative;
		padding: calc(clamp(10px, 2vw, 50px));
		cursor: pointer;
		margin: 0 5px;
	}
	ul.right-side .back-page span,
	ul.right-side .next-page span {
		position: absolute;
		left: 50%;
		top: 50%;
		width: 50%;
		height: 50%;
		border-left: 1px solid var(--light-color);
		border-bottom: 1px solid var(--light-color);
	}
	ul.right-side .back-page::before,
	ul.right-side .next-page::before {
		content: "";
		position: absolute;
		inset: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		border: solid 0.5px var(--background-color3);
		border-radius: 50%;
	}
	ul.right-side .back-page:hover::before,
	ul.right-side .back-page.active::before,
	ul.right-side .next-page:hover::before,
	ul.right-side .next-page.active::before {
		-webkit-animation: wave 1.2s linear infinite;
		animation: wave 1.2s linear infinite;
	}
	@-webkit-keyframes wave {
		0% {
			opacity: 0;
			transform: scale(0);
		}
		50% {
			opacity: 1;
			transform: scale(1.2);
		}
		100% {
			transform: scale(1.5);
			opacity: 0;
		}
	}
	@keyframes wave {
		0% {
			opacity: 0;
			transform: scale(0);
		}
		50% {
			opacity: 1;
			transform: scale(1.2);
		}
		100% {
			transform: scale(1.5);
			opacity: 0;
		}
	}
	ul.right-side .back-page::after,
	ul.right-side .next-page::after {
		z-index: -1;
		content: "";
		position: absolute;
		height: 50px;
		width: 100%;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
	}
	ul.right-side .back-page span {
		transform: translate(-20%, -45%) rotate(45deg);
	}
	ul.right-side .next-page span {
		transform: translate(-70%, -45%) rotate(-135deg);
	}
}

.home {
