	--background-color4: rgb(20, 20, 21);
	--light-color: rgb(161, 158, 158);
	--word-color: white;
	--primary-color1: #5d3cde;
	--secondary-color1: #acaaff;
	--accent-color1: #c479ff;
	--animation-delay: 0.7s;
}

* {
	margin: 0;
	padding: 0;
	list-style: none;
	font-family: "Inter", sans-serif;
	-webkit-appearance: none;
}

.br::before,
.br-work::before {
	content: "\A";
