	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100vh;
	background-color: var(--background-color1);
}

body {
	position: relative;
	width: 100%;
	height: 100%;
	border-radius: 10px;
	overflow: hidden;
}

#root {
	position: absolute;
	width: 100%;
	height: 100%;
	inset: 0;
	margin: auto;
}

.container {
	position: relative;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.main-width {
	position: relative;
	width: 100%;
	max-width: 3840px;
	padding-bottom: calc(min(56.25%, 2160px));
	background-color: var(--background-color2);
}

.main-width .main-container {
	position: absolute;
	width: 100%;
	height: 100%;
}

.content {
	-webkit-animation: content-appear 1s forwards;
	animation: content-appear 1s forwards;
	-webkit-animation-delay: calc(var(--animation-delay) - 0.5s);
	animation-delay: calc(var(--animation-delay) - 0.5s);
	opacity: 0;
}

@-webkit-keyframes content-appear {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@keyframes content-appear {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

.sColor {
	color: var(--primary-color1);
	background: linear-gradient(to right top, var(--primary-color1), var(--secondary-color1));
	-webkit-background-clip: text;
	background-clip: text;
	-webkit-text-fill-color: transparent;
	text-fill-color: transparent;
}

.line::before,
.line::after {
	position: absolute;
	content: "";
	height: 35%;
	width: 0.2%;
	background-color: var(--primary-color1);
}

.title {
	position: absolute;
	left: 5%;
	top: 50%;
	transform: translateY(50%) rotate(-90deg);
	transform-origin: left;
	color: var(--light-color);
	z-index: 110;
	background-color: var(--background-color2);
}

.title h1 {
	font-size: 6rem;
	letter-spacing: 0.5rem;
	font-family: "Inria Serif", serif;
	font-weight: 300;
}

.line {
	z-index: -1;
	position: absolute;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: space-between;
}

.line::before {
	left: 5%;
	top: -12.5%;
}

.line::after {
	right: 5%;
	bottom: -12.5%;
}

.secondary-color {
	color: var(--secondary-color1);
	-webkit-filter: drop-shadow(1px 1px 5px black);
	filter: drop-shadow(1px 1px 5px black);
}

p,
h1,
h2 {
	-webkit-filter: drop-shadow(1px 1px 5px black);
	filter: drop-shadow(1px 1px 5px black);
