	}
	body {
		position: relative;
		height: 100%;
		padding-bottom: 0;
		border-radius: 0;
		overflow-x: hidden;
		overflow-y: auto;
	}
	.container {
		height: 100%;
		align-items: flex-start;
	}
	.line {
		display: none;
	}
	.title {
		display: block;
		position: relative;
		width: 100%;
		text-align: center;
		top: 0;
		left: 0;
		transform: translateY(0) rotate(0deg);
		margin: 0 0 10% 0;
		padding: 1% 0;
	}
	.title::before,
	.title::after {
		position: absolute;
		content: "";
		width: 80%;
		height: 3px;
		background-color: var(--primary-color1);
	}
	.title::before {
		top: 0;
		left: 0;
	}
	.title::after {
		bottom: 0;
		right: 0;
	}
	.content {
		position: relative;
		flex-direction: column;
		width: 100%;
		height: auto;
		justify-content: flex-start;
		overflow: hidden;
		top: 50px;
		left: 0;
		transform: translate(0, 0);
		padding-bottom: 100px;
	}
	.content .main {
		position: relative;
		top: 0;
		margin-top: 50px;
		width: 90%;
		left: 0;
		transform: translate(0, 0);
	}
}

@media (max-width: 500px) {
	.br::before {
		content: "";
		white-space: auto;
	}
	.title h1 {
		font-size: 5rem;
	}
}

@media (max-width: 385px) {
	.br-work::before {
		content: "";
		white-space: auto;
	}
}
